
import React from 'react';
import {useState, useEffect, createContext, useContext} from "react";
import {Navbar, Container, Nav, NavDropdown, Form, FormControl, Button, Row, Col} from 'react-bootstrap';
import PropTypes from 'prop-types';
import 'bootstrap/dist/css/bootstrap.min.css';

function NavigationBarTwo({dark=true, brandName, left, center, right }){
  return(
    <Navbar bg={dark?'dark':'light'} variant={dark?'dark':'light'} expand="lg">
        <Container fluid>
          <Row>
            <Col>
              <Nav>
                <Navbar.Brand href="#">{brandName}</Navbar.Brand>
                {left}
              </Nav>
            </Col>
            <Col>
              <Nav className="justify-content-center">
                {center}
              </Nav>
            </Col>
            <Col>
              {right}
            </Col>
          </Row>
        </Container>
    </Navbar>
  )
}

NavigationBarTwo.propTypes={
  dark: PropTypes.bool,
  brandName: PropTypes.string,
  left: PropTypes.element,
  center: PropTypes.element,
  right: PropTypes.element
}


function NavigationBar({dark=true, brandName="Brand Name", left, right }){
    return(
    <Navbar bg={dark?'dark':'light'} variant={dark?'dark':'light'} expand="lg">
      <Container fluid>
        <Navbar.Brand href="#">{brandName}</Navbar.Brand>
        <Navbar.Toggle/>
        <Navbar.Collapse>
          <Nav className="me-auto my-2 my-lg-0" style={{ maxHeight: '100px' }} navbarScroll>
            {left}
          </Nav>
          {right}
        </Navbar.Collapse>
      </Container>
    </Navbar>
    )
}

NavigationBar.propTypes={
  dark: PropTypes.bool,
  brandName: PropTypes.string,
  left: PropTypes.element, 
  right: PropTypes.element
}

function NavItems ({lable, onClick, style}){
  return <Nav.Link style={style} onClick={onClick}>{lable}</Nav.Link>
}

NavItems.prototype={
  lable:PropTypes.string,
  onClick:PropTypes.func,
  style:PropTypes.object,
}

function DropDownItem({lable, title}){
  return(
    <Nav>
      <NavDropdown title={title} menuVariant="dark"  >
        {lable.map((v, i)=>{
          return  <NavDropdown.Item key={i} onClick={v.onClick}>{v.lable}</NavDropdown.Item>
        })}
      </NavDropdown>
    </Nav>
  )
}

DropDownItem.prototype={
  lable:PropTypes.object,
  onClick:PropTypes.func,
  title: PropTypes.string,
}

export {NavigationBar, NavItems, DropDownItem, NavigationBarTwo}


